// Adapted from https://github.com/chenglou/react-motion/tree/master/demos/demo8-draggable-list
import React, { Component } from "react";
import { render } from "react-dom";
import NodeGroup from "react-move/NodeGroup";
import { range } from "d3-array";
import { easeExpOut } from "d3-ease";
import "./demo.css";

function reinsert	(arr, beg, end) {
  const copy = arr.slice(0);
  const val = copy[beg];
  copy.splice(beg, 1);
  copy.splice(end, 0, val);
  return copy;
}

function clamp(n, min, max) {
  return Math.max(Math.min(n, max), min);
}

function stopEvent(e) {
  e.preventDefault();
  e.stopPropagation();
}

const itemsCount = 5;
const itemHeight = 75; // set list-item height and line-height in css as well



class Demo extends Component {
  state = {
    topDeltaY: 0,
    mouseY: 0,
    isPressed: false,
    lastPressed: 0,
    order: range(itemsCount)
  };

  handleTouchStart = (key, pressLocation, e) => this.handleMouseDown(key, pressLocation, e.touches[0])
  handleTouchMove = e => e.preventDefault() || this.handleMouseMove(e.touches[0])
  handleMouseUp = () => this.setState({ isPressed: false, topDeltaY: 0 })
  handleMouseDown = (pos, pressY, { pageY }) =>
    this.setState({ topDeltaY: pageY - pressY, mouseY: pressY, isPressed: true, originalPosOfLastPressed: pos })
  handleMouseMove = ({ pageY }) => {
    const { isPressed, topDeltaY, order, originalPosOfLastPressed } = this.state
    if (isPressed) {
      const mouseY = pageY - topDeltaY
      const currentRow = clamp(Math.round(mouseY / 100), 0, itemsCount - 1)
      let newOrder = order
      if (currentRow !== order.indexOf(originalPosOfLastPressed))
        newOrder = reinsert(order, order.indexOf(originalPosOfLastPressed), currentRow)
      this.setState({ mouseY: mouseY, order: newOrder })
    }
  }
  componentDidMount() {
    window.addEventListener("contextmenu", stopEvent, { passive: false });
    window.addEventListener('touchmove', this.handleTouchMove, {passive:false});
    window.addEventListener('touchend', this.handleTouchEnd, {passive:false});
    window.addEventListener('mousemove', this.handleMouseMove, {passive:false});
    window.addEventListener('mouseup', this.handleMouseUp, {passive:false});
  }

  componentWillUnmount() {
    window.removeEventListener("contextmenu", stopEvent, { passive: false });
  }


  render() {
    const { mouseY, isPressed, lastPressed, order } = this.state;

    return (
      <div className="demo-container">
        <NodeGroup
          data={range(itemsCount)}
          keyAccessor={d => `item-key-${d}`}
          start={d => ({
            scale: 1,
            shadow: 1,
            y: order.indexOf(d) * itemHeight
          })}
          update={d => {
            const dragging = lastPressed === d && isPressed;

            return {
              scale: [dragging ? 1.1 : 1],
              shadow: [dragging ? 5 : 1],
              y: [order.indexOf(d) * itemHeight],
              timing: { duration: 350, ease: easeExpOut }
            };
          }}
        >
          {nodes => (
            <div className="list">
              {nodes.map(({ key, data, state }) => {
                const { shadow, scale, y } = state;
                const transY = lastPressed === data && isPressed ? mouseY : y;

                return (
                  <div
                    className="list-item"
                    key={key}
                    onMouseDown={e => this.handleMouseDown(data, y, e)}
                    onTouchStart={e => this.handleTouchStart(data, y, e)}
                    style={{
                      boxShadow: `rgba(0, 0, 0, 0.4) 0px ${shadow}px ${2 *
                        shadow}px 0px`,
                      transform: `translate3d(0, ${transY}px, 0) scale(${scale})`,
                      WebkitTransform: `translate3d(0, ${transY}px, 0) scale(${scale})`,
                      zIndex: data === lastPressed ? 99 : data
                    }}
                  >
                    {order.indexOf(data) + 1}
                  </div>
                );
              })}
            </div>
          )}
        </NodeGroup>
      </div>
    );
  }
}

render(<Demo />, document.getElementById("root"));



